<template>
  <div class="content">
    <div class="md-layout">
      <!-- Hiện thông báo Các trường chưa nhập thông tin -->
      <md-dialog :md-active.sync="showDialog">
        <md-dialog-title>Thông báo</md-dialog-title>
        <md-dialog-content
          >Bạn chưa nhập các trường: <b>{{ message }}</b></md-dialog-content
        >
        <md-dialog-actions>
          <md-button class="md-warning" @click="showDialog = false"
            >Đóng</md-button
          >
        </md-dialog-actions>
      </md-dialog>

      <!-- Nhập Input -->
      <div class="md-layout-item md-medium-size-100 md-size-70">
        <form>
          <md-card>
            <md-card-header :data-background-color="dataBackgroundColor">
              <h4 class="title"><b>Thông tin Cơ sở dữ liệu PostgreSQL</b></h4>
            </md-card-header>

            <md-card-content>
              <div class="md-layout">
                <div class="md-layout-item md-small-size-100 md-size-50">
                  <md-field>
                    <label>Server Name</label>
                    <md-input
                      v-model="servername"
                      type="text"
                      required
                      :disabled="serverDis"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-small-size-100 md-size-50">
                  <md-field>
                    <label>Port</label>
                    <md-input
                      v-model="port"
                      type="text"
                      required
                      :disabled="portDis"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-small-size-100 md-size-50">
                  <md-field>
                    <label id="message_pg_user">User Name</label>
                    <md-input
                      v-model="username"
                      type="text"
                      required
                      :disabled="userDis"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-small-size-100 md-size-50">
                  <md-field>
                    <label>Password</label>
                    <md-input
                      v-model="password"
                      type="password"
                      required
                      :disabled="passDis"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-small-size-100 md-size-100">
                  <md-field>
                    <label>Database Name</label>
                    <md-input
                      v-model="dbname"
                      type="text"
                      required
                      :disabled="dbnameDis"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-small-size-100 md-size-50">
                  <md-content
                    >Tình trạng kết nối:
                    <b :style="color_status">{{ connect_status }}</b>
                  </md-content>
                </div>
                <div class="md-layout-item md-size-100 md-size-50 text-right">
                  <md-button
                    :class="buttonConnect"
                    @click="
                      [
                        sendconfig(),
                        checkempty(),
                        changeButton(),
                        (checkConn += 1)
                      ]
                    "
                  >
                    {{ connect_attr }}
                  </md-button>
                </div>
              </div>
            </md-card-content>
          </md-card>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props: {
    dataBackgroundColor: {
      type: String,
      default: "blue"
    }
  },
  data: () => ({
    /*** Hiện dialog kiểm tra các trường ***/
    showDialog: false,
    message: "",
    /*** Đóng các trường ***/
    serverDis: false,
    portDis: false,
    userDis: false,
    passDis: false,
    dbnameDis: false,
    /*** Change Button ***/
    color_status: {
      color: ""
    },
    connect_status: "Chưa kết nối",
    connect_attr: "Kết nối",
    buttonConnect: "md-raised md-info",
    /*** Thông tin kết nối ***/
    servername: "localhost",
    port: "5432",
    username: "postgres",
    password: "0888365051",
    dbname: "travinh_quantrac",
    /*** Kiểm tra kết nối ***/
    checkConn: 0
  }),
  methods: {
    sendconfig: async function() {
      var res = "";
      await axios
        .get("http://localhost:2502/tool_auto_td/config.php", {
          params: {
            servername: this.servername,
            port: this.port,
            username: this.username,
            password: this.password,
            dbname: this.dbname
          }
        })
        .then(function(response) {
          res = response.data;
        })
        .catch(function(error) {
          Console.log(error);
        });
      if (res === "success") {
        this.connect_status = "Kết nối thành công";
        this.color_status.color = "#0a8500";
      } else {
        this.connect_status = "Kết nối thất bại";
        this.color_status.color = "#FF0000";
      }
      return res;
    },
    checkempty: function() {
      this.message = "";
      if (this.servername === "") {
        this.showDialog = true;
        this.message += "Servername ";
      }
      if (this.port === "") {
        this.showDialog = true;
        this.message += "Port ";
      }
      if (this.username === "") {
        this.showDialog = true;
        this.message += "Username ";
      }
      if (this.password === "") {
        this.showDialog = true;
        this.message += "Password ";
      }
      if (this.dbname === "") {
        this.showDialog = true;
        this.message += "Database Name";
      }
    },
    changeButton: async function() {
      var check_connect = await this.sendconfig();
      if (this.checkConn % 2 !== 0 && this.checkConn !== 0) {
        if (check_connect === "success") {
          this.connect_attr = "Ngắt kết nối";
          this.buttonConnect = "md-raised md-danger";
          /*** Disable Input ***/
          this.serverDis = true;
          this.portDis = true;
          this.userDis = true;
          this.passDis = true;
          this.dbnameDis = true;
        } else {
          this.checkConn -= 1;
        }
      } else if (this.checkConn % 2 === 0 && check_connect === "success") {
        this.connect_status = "Đã ngắt kết nối";
        this.color_status.color = "#000000";
        this.connect_attr = "Kết nối";
        this.buttonConnect = "md-raised md-info";
        /*** Disable Input ***/
        this.serverDis = false;
        this.portDis = false;
        this.userDis = false;
        this.passDis = false;
        this.dbnameDis = false;
      }
    }
  }
};
</script>

<style></style>
