import DashboardLayout from "@/pages/Layout/DashboardLayout.vue";

import HomeComponent from "@/pages/Layout/HomeComponent.vue";
import FTPProfile from "@/pages/Form/EditFTPForm.vue";
import FTPtotal from "@/pages/Form/EditFTPtotalForm.vue";
import PostgreSQL from "@/pages/Form/EditDBForm.vue";

const routes = [
  {
    path: "/",
    component: DashboardLayout,
    redirect: "home",
    children: [
      {
        path: "home",
        name: "Trang chính",
        component: HomeComponent
      },
      {
        path: "ftp",
        name: "FTP",
        component: FTPProfile
      },
      {
        path: "ftptotal",
        name: "FTP Tổng cục",
        component: FTPtotal
      },
      {
        path: "postgres",
        name: "Cơ sở dữ liệu PostgreSQL",
        component: PostgreSQL
      }
    ]
  }
];

export default routes;
